<script>
import Nav from "@/components/Nav.vue";
import {
    getFlowerPrice,
    getFlowerPrices,
    getFlowerRank,
} from "../../service/flower";
import { setFlowerServer, getServer } from "../../service/server";
import dateFormat from "../../utils/moment";
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
import colors from "./colors.json";
import flowers from "./flowers.json";
import { __iconPath } from "@jx3box/jx3box-common/js/jx3box.json";

export default {
    name: "Flower",
    props: [],
    data: function() {
        return {
            servers,
            server: "梦江南",
            types: ["绣球花", "郁金香", "牵牛花", "玫瑰", "百合", "荧光菌"],
            type: "",
            level: "",
            colors,
            flowers,

            rank: "",
            overview: "",
            data: [],

            total: 1,
            pages: 1,
            page: 1,
            loading: false,
            done: false,
        };
    },
    computed: {
        levels: function() {
            let levels = [];
            if (this.type) {
                return Object.keys(this.colors[this.type]);
            }
            return levels;
        },
        isGuest: function() {
            // return !User.isLogin();
            return false;
        },
        hasNextPage: function() {
            return this.total > 1 && this.page < this.pages;
        },
        mode: function() {
            if (!this.type && !this.level) {
                return 0;
            } else if (this.type && !this.level) {
                return 1;
            } else if (this.type && this.level) {
                return 2;
            }
            return 0;
        },
    },
    methods: {
        color: function(level) {
            if (this.type) {
                return this.colors[this.type][level];
            }
            return "";
        },
        search: function() {
            this.page = 1; //复位

            // 概览模式
            if (this.mode == 0) {
                this.loadRank();
                // 简略模式
            } else if (this.mode == 1) {
                this.loadOverview();
                // 完整模式
            } else {
                this.loadData(1);
            }
            setFlowerServer(this.server);
        },
        dateFormat: function(row, column) {
            return dateFormat(row.time * 1000);
        },
        nameFormat: function(row, column) {
            let colors = [];
            flowers[row.name].forEach((item) => {
                colors.push(item.color);
            });
            colors = colors.join("/");
            return row.name + " ( " + colors + " ) ";
        },
        loadRank: function() {
            this.loading = true;
            return getFlowerRank(this.server, this).then((data) => {
                let list = [];
                for (let name in flowers) {
                    let lines = data[name]
                        ? data[name]["maxLine"].slice(0, 3)
                        : [];
                    lines.forEach((item, i) => {
                        lines[i] = item && item.replace(" 线", "");
                    });

                    let max = data[name] ? ~~data[name]["max"] : "-";
                    list.push({
                        name,
                        line: lines,
                        price: max,
                    });
                }
                this.rank = list;
            });
        },
        loadOverview: function() {
            this.loading = true;
            return getFlowerPrices(
                {
                    server: this.server,
                    flower: this.type,
                },
                this
            ).then((res) => {
                let overview = [];
                for (let name in res.data) {
                    overview.push({
                        name: name,
                        price: res.data[name]["max"] + "园宅币",
                        map: res.data[name]["maxLine"].slice(0, 5),
                    });
                }
                this.overview = overview;
            });
        },
        loadData: function(i, append = false) {
            this.loading = true;
            return getFlowerPrice(
                {
                    server: this.server,
                    flower: this.level + this.type,
                    pageIndex: i,
                },
                this
            ).then((res) => {
                if (append) {
                    this.data = this.data.concat(res.data.data);
                } else {
                    this.data = res.data.data;
                }
                this.total = res.data.page.total;
                this.pages = res.data.page.pageTotal;
            });
        },
        appendPage: function(i) {
            this.loading = true;
            this.loadData(i, true);
        },
        changePage: function(i) {
            this.loading = true;
            this.loadData(i).then(() => {
                window.scrollTo(0, 0);
            });
        },
        onCopy: function(val) {
            this.$notify({
                title: "复制成功",
                message: "复制内容 : " + val.text,
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "复制失败",
                message: "请手动复制",
            });
        },
        onlyLineNumber: function(val) {
            return val.replace("线", "");
        },
    },
    filters: {
        iconURL: function(id) {
            return __iconPath + "icon/" + id + ".png";
        },
    },
    mounted: function() {
        getServer("flower_server")
            .then((server) => {
                if (server) {
                    this.server = server;
                }
            })
            .then(() => {
                this.loadRank();
            });
    },
    components: {
        Nav,
    },
};
</script>

<style lang="less">
